.journey {
  color: $color-tangaroa;
}

.journey__container {
  display: flex;
  padding-bottom: 145px;
  gap: 88px;

  @include vp-1439 {
    padding-bottom: 145px;
  }

  @include vp-1023 {
    padding-bottom: 93px;
    gap: 25px;
  }

  @include vp-767 {
    padding-bottom: 85px;
    flex-direction: column;
    align-items: center;
    gap: 42px;
  }
}

.journey__description {
  max-width: 512px;
  padding-top: 56px;

  @include vp-1023 {
    max-width: 324px;
    padding-top: 0;
  }

  @include vp-767 {
    max-width: none;
  }
}

.journey__heading {
  margin-bottom: 24px;

  @include vp-1023 {
    margin-top: -9px;
    margin-bottom: 16px;
    padding-right: 30px;
  }

  @include vp-767 {
    margin-bottom: 15px;
    text-align: left;
  }
}

.journey__text {
  margin: 0;
  font-size: 16px;
  line-height: 24px;
}

.journey__background {
  @include vp-1023 {
    padding-right: 5px;
  }

  @include vp-767 {
    padding-right: 0;
  }
}
